#order-list-cont-header>div {
	text-align: left;
	font-size: 25px;
	color:#3a3a3a;
}

.underline {
	background-color: #3a3a3a;
	height: 5px;
	width: 100%;
	border-radius: 5px;
}

.order-list-amount::before {
	content: '$';
}

.order-list-inner-cont {
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	grid-template-rows: repeat(4, 1fr);
	gap: 10px; /* Adjust the gap value to increase/decrease the spacing between the grid items */
	margin-top: 5px;
	margin-bottom: 5px;
	border-radius: 5px;
	overflow: hidden;
}

.order-list-order {
	padding: 20px; /* Adjust the padding value to increase/decrease the space inside each grid item */
	border-radius: 5px;
	background-color: #fefefe;
	border: 1px solid #ebebeb;
}

.order-list-takenby {
	font-size: 15px;
	text-align: left;
	font-weight: 300;
	font-style: italic;
}

.order-list-order:hover {
	background-color: #ebebeb;
}

.order-list-order-header>div {
	text-align: left;
	font-size: 40px;
	color: #3a3a3a;
}

.order-list-objs {
	text-align: left;
}
.order-list-date {
	font-weight: 525;
	color: #3a3a3a;
}

.order-list-shipTo {
	font-weight: 550;
	font-size: 15px;
	color: #3a3a3a;
}

.order-list-amount {
	font-size: 20px;
	color: #3a3a3a;
}

#trackedOrders {
	max-height: 85vh;
	max-width: 90vw;
	min-height: 85vh;
	min-width: 90vw;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    color:#282c34;
    font-weight: 600;
    filter: none;
	position: absolute;
	top: 5vh;
	left: 5vw;
}