#metal-supply-form {
	min-height: 95vh;
	max-height: 95vh;
	min-width: 97.5vw;
	max-width: 97.5vw;
}

#form-cont {
	height: 100vh;
	width: 100vw;
	display: grid;
	align-items: center;
	justify-content: center;
}

#metal-supply-form>.card-content {
	display: flex;
	height: calc(95vh - 40px);
}

.info-sidebar {
	width: 30%;
}

.order-cont {
	width: 70%;
}

.form-header {
	text-align: left;
	margin-left: 10px;
}

.outline-card {
	border-radius: 15px !important;
	border: 2px solid var(--titlebar-bg);
	background-color: #fafafa !important;
	max-width: 100%;
	min-width: 100%;
	height: 70vh;
}

/*ORDER INFORMATION TAB*/
#order-info-card {
	min-height: 82.5vh;
	max-height: 82.5vh;
}

#order-info-card-card-content {
	display: grid;
	width: 90%;
	margin-left: 5%;
	margin-right: 5%;
}

#order-info-card-card-content>.form-info-item {
	width: 100%;
	height: 6.85vh;
	display: grid;
	grid-template-columns: 20% 75%;
	align-items: center;
	justify-content: center;
	gap: 5%;
}

#order-info-card-card-content>.form-info-item>.form-info-title {
	font-size: larger;
	font-weight: 600;
	text-align: right;
	width: 100%;
}

#order-info-card-card-content>.form-info-item>input,
#order-info-card-card-content>.form-info-item>.dropdown-cont,
#order-info-card-card-content>.form-info-item>.dropdown-cont>.dropdown-option {
	width: 100%;
	height: 40px;
	font-size: larger;
}

#order-info-card-card-content>.form-info-item>.dropdown-cont>.dropdown-list {
	width: 100%;
	position: relative;
	left: 5px;
	bottom: 5px;
}


/*ORDER SUMMARY TAB*/
/*Order Items Card*/
#order-items-card>.card-content {
	padding-bottom: 0px;
	padding-left: 0px;
	padding-right: 0px;
	height: calc(100% - 40px);
}

#order-items-table-cont {
	height: 85%;
	padding: 0;
}

#order-items-card-card-content {
	padding: 0;
}

#order-items-table-cont>.tablerow.header {
	padding: 0;
	margin: 0;
	height: 40px;
	font-size: 20px;
	line-height: 40px;
	grid-template-columns: 8% 16% 32% 16% 16% 8%;
	background-color: #09406f;
}

#order-items-table-cont>.tablerow.header>div {
	background-color: transparent;
	color:#fff;
	font-weight: 550;
	padding: 0;
	margin: 0;
}

#order-items-table {
	height: 51.5vh;
	overflow: overlay;
	padding: 0;
	margin: 0;
}

#order-items-table>.tablerow {
	height: 50px;
	padding: 0;
	margin: 0;
	background-color: transparent;
	grid-template-columns: 8% 16% 32% 16% 16% 10%;;
	font-size: 25px;
}
#order-items-table>.tablerow:hover {
	background-color: #ccc;
}

#order-items-add {
	height: 15%;
	border-top: 2px solid var(--titlebar-bg);
	display: flex;
	align-items: center;
	gap: 2.5%;
}

/*Add Item Row*/
#items-form {
	width: 80%;
	height: 100%;
	display: grid;
	grid-template-columns: 35% 65%;
	align-items: center;
	gap: 10px;
}

#items-form>.form-info-item {
	height: 100%;
	align-items: center;
	gap: 20px;
}
#items-form>.form-info-item>.form-info-title {
	font-weight: 450;
	font-size: 17.5px;
	text-align: right;
}
#items-form>.form-info-item>input {
	height: 40px;
	font-size: medium;
}

/*Form Items*/
#order-qty-form-cont>.form-info-title {
	width: 45%;
}
#order-qty-form-cont>input {
	width: 55%;
}

#order-part-num-form-cont>.form-info-title {
	width: 45%;
}
#order-part-num-form-cont>input {
	width: 50%;
}

#order-descr-form-cont>.form-info-title {
	width: 30%;
}
#order-descr-form-cont>input {
	width: 70%;
}

#order-price-form-cont>.form-info-title {
	width: 50%;
}
#order-price-form-cont>input {
	width: 50%;
}

#item-descr-form {
	display: grid;
	grid-template-columns: 20% 80%;
}
#item-descr-form>.dropdown-cont {
	width: 100%;
	position: relative;
}
#item-descr-form>.dropdown-cont>.dropdown-option {
	width: 100%;
}
#item-descr-form>.dropdown-cont>.dropdown-option>.dropdown-arrow {
	right: 4%;
}
#item-descr-form>.dropdown-cont>.dropdown-option>.dropdown-clear {
	right: 10%
}

#item-descr-form>.dropdown-cont>.dropdown-list {
	width: 100%;
	position: absolute;
	bottom: 30px;
	left: 5px;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
	border-bottom-right-radius: 0px;
	border-bottom-left-radius: 0px;
}

#add-item {
	background-color: #64965b;
	font-weight: 650;
	font-size: larger;
}
#add-item:hover {
	background-color: #476d40;
}

/*Invoice "Card"*/
#invoice-cont {
	height: 15%;
	padding: 0;
	display: flex;
}

#invoice-total {
	width: 70%;
}

#invoice-total>.form-info-item {
	width: 50%;
	display: grid;
	grid-template-columns: 40% 30%;
	align-items:end;
	justify-content: end;
	font-size:x-large;
	margin-left: 45%;
}
#invoice-total>:nth-child(4) {
	font-weight: 600;
}

#invoice-total>.form-info-item>.form-info-title {
	text-align: right;
	width: 90%;
}

#invoice-total>.form-info-item>:nth-child(2)::before {
	content: "$";
}

#invoice-total>.form-info-item>:nth-child(2) {
	text-align: right;
	width: 90%;
}

/*Submit/Cancel Buttons*/
#form-close-actions {
	height: 100%;
	width: 30%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 0.75%;
}
#form-close-actions>.action-button {
	min-height: 75%;
	max-height: 75%;
	font-size: 40px;
	line-height: 85px;
	font-weight: 600;
	vertical-align: middle;
}
#submit-form {
	background-color: rgb(100, 121, 184);
}
#submit-form:hover {
	background-color: rgb(68, 86, 138);
}

#cancel-form {
	background-color: rgb(184, 100, 100);
}
#cancel-form:hover {
	background-color: rgb(138, 68, 68);
}

#form-close-card {
	max-height: 12vh;
	min-height: 12vh;
	min-width: 20vw;
	max-width: 20vw;
}
#form-close-card {
	height: 100%;
}
#form-close-card>div>:first-child {
	height: 50px;
	font-size: 20px;
}
#form-close-card>div>.action-row {
	justify-content: center;
	align-content: center;
}
#form-close-card>div>.action-row>.action-button {
	height: 50px;
	width: 150px;
	font-size: 15px;
	display: grid;
	align-items: center;
}

#cancel-yes, #submit-yes {
	background-color: #45803a;
}
#cancel-yes:hover, #submit-yes:hover {
	background-color:#5c9b51;
}

/*BASE STYLES*/
input.form-item-empty {
	border-bottom: 2.5px solid rgb(157, 45, 45);
}

/*DOCK*/
#clear-orders {
	width: 150px;
	background-color: #45803a;
}
#clear-orders:hover {
	width: 150px;
	background-color:#476d40;
}


/*SCROLLBAR WEBKIT*/
*::-webkit-scrollbar {
    display: block;
    width: 8px;
}
*::-webkit-scrollbar-button {
    display: none;
}
*::-webkit-scrollbar-track {
    background-color: #00000000;
}
*::-webkit-scrollbar-track-piece {
    background-color: #00000000;
}
*::-webkit-scrollbar-thumb {
    background-color: #000000c7;
    border: 1px solid #00000040;
    border-radius: 12px;
}